@import '_variables';
@import '_mixins';

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.input-container {
  @include d-flex(column, center, center);
  position: relative;

  padding: 30px 25px;
  border-radius: 15px;
  background-color: rgb(246, 248, 250);
  border: 1px solid rgba(128, 128, 128, 0.514);
  box-shadow: 2px 3px 15px rgba(128, 128, 128, 0.74);
  width: 360px;
  height: auto;
  
  .forgot-container {
    @include fadeIn(1s);
    margin-top: 15px;

    a {
      color: $link-text-color;
      text-decoration: none;
      font-style: normal;
      font-family: 'Ubuntu', sans-serif;
      letter-spacing: 0px;
  
      &:hover {
        text-decoration: underline;
      }
    }

  }
}

img {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -1;
  filter: brightness(50%);
}

.register-container {
  @include d-flex('', space-around, center);

  width: 360px;
  height: 50px;
  margin-top: 5px;
  border-radius: 15px;
  background-color: rgb(246, 248, 250); 
  opacity: 85%;
  border: 1px solid rgba(128, 128, 128, 0.514);

  a {
    color: $link-text-color;
    text-decoration: none;
    font-style: normal;
    font-family: 'Ubuntu', sans-serif;

    &:hover {
      text-decoration: underline;
    }
  }
}

.login-container {
  @include d-flex(column, space-between, center);
  position: relative;
  overflow: hidden;
  z-index: 10;
  opacity: 87%;
  border-radius: 7px;
  width: 390px;
  height: auto;
  color: black !important;
  font-family: 'Langar', cursive;
  letter-spacing: 1.5px;
  transition: width .3s ease-in-out, height .3s ease-in-out;

  form {
    @include d-flex(column, center, center);
    @keyframes formFadeIn {
      100% {
        opacity: 100%;
      }
    }
    opacity: 0;
    height: auto;
    width: 100%;
    padding: 15px;
    animation: .3s formFadeIn .3s ease-in-out forwards;
  }

  h1 {
    @include d-flex('', center, center);

    color: white;
    font-family: 'Ubuntu', sans-serif;
    position: relative;
    text-align: center;
    font-size: 35px;
    margin: 13px 15px;
    margin-bottom: 60px;
    width: fit-content;

    &::after {
      @include simpleAnim(width, 80%, 1.5s, 0s, ease-in-out);

      z-index: 5;
      content: '';
      position: absolute;
      border: 3px solid $login-object-theme;
      border-radius: 15px;
      bottom: -15px;
    }
  }

  .textbox {
    width: 100%;
    position: relative;
    overflow: hidden;
    font-size: 20px;
    padding: 30px 0;
    margin: 8px 0;
    border-bottom: 1px solid $login-object-theme;

    i {
      position: absolute;
      width: 26px;
      float: left;
      text-align: center;
      bottom: 12px;
    }

    .underline {
      position: absolute;
      height: 2px;
      width: 100%;
      bottom: 0px;

      &:before {
        position: absolute;
        content: "";
        height: 100%;
        width: 100%;
        background: $login-object-theme;
        transform: scaleX(0);
        transform-origin: center;
        transition: transform .3s ease;
        bottom: 0;
      }

      &.incorrect::before {
        background: $danger-red !important;
      }
    } // underline

    label {
      z-index: 1;
      position: absolute;
      bottom: 8px;
      left: 36px;
      color: rgb(92, 92, 92);
      pointer-events: none;
      transition: all .3s ease;
      font-size: 20px;

      .required {
        color: red;
      }
    }

    input {
      z-index: 2;
      position: absolute;
      border: none;
      outline: none;
      background: none;
      color: $login-text-theme;
      font-size: 18px;
      width: 90%;
      float: left;
      bottom: 10px;
      margin: 0 10px;
      left: 20px;

      &:-webkit-autofill,
      &:-webkit-autofill:hover,
      &:-webkit-autofill:focus {
        -webkit-text-fill-color: $login-text-theme;
        background-color: transparent;
        box-shadow: 0 0 0px 1000px transparent inset;
        transition: background-color 5000s ease-in-out 0s;
      }

      &:focus ~ label,
      &:valid ~ label {
        transform: translateY(-27px) translateX(-30px);
        font-size: 15px;
        font-weight: bold;
        color: $login-object-theme;
      }

      &:focus ~ .underline:before,
      &:valid ~ .underline:before {
        transform: scaleX(1);
      }
    } // input
  } // .textbox

  .btn {
    width: 100%;
    background: none;
    border: 2px solid $login-object-theme;
    color: $login-text-theme;
    padding: 5px;
    font-size: 19px;
    cursor: pointer;
    margin: 12px 12px;
    border-radius: 5px;
    transition: all .5s ease;
  } //.btn

  .btn-cont {
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-top: 35px;

    #signin-btn {
      border-color: #2c974b;
      color: #2c974b;
      margin-right: 0px;
      transition: color, .3s ease, background-color .3s ease;

      &:hover {
        background-color: #2c974b;
        color: white;
        transition: color, .3s ease, background-color .3s ease;
      }
    }

    #back-btn {
      margin-left: 0px;
      border-color: $login-object-theme;
      color: $login-text-theme;
      font-weight: bold;

      &:hover {
        background-color: $login-object-theme;
        color: white;
      }
    }
  } // .btn-cont

  @keyframes shake {
    10%,
    90% {
      transform: translate3d(-1px, 0, 0);
    }

    20%,
    80% {
      transform: translate3d(2px, 0, 0);
    }

    30%,
    50%,
    70% {
      transform: translate3d(-4px, 0, 0);
    }

    40%,
    60% {
      transform: translate3d(4px, 0, 0);
    }
  }

  .shake {
    animation: shake 0.82s;
  }

  .im-lock.incorrect,
  .im-user-circle.incorrect,
  .textbox.password label.incorrect,
  .textbox.username label.incorrect {
    color: red;
  }

  #title-incorrect {
    color: #ff0000;
    margin: 0;
    padding: 0;
    display: relative;
    position: absolute;
    left: 25px;
    top: 15px;
    transition: all .5s ease;
  }

  #title {
    width: 100%;
  }
} // form


@media (max-width: 350px) {
  body {
    justify-content: flex-start;
  }

  .login-container {
    width: 290px;
    height: auto;
  }

  #title {
    margin-bottom: 25px;
    margin-top: 0;
  }

  .input-container {
    width: 290px;

    .btn-cont {
      margin-top: 15px;
    }

    .textbox {

      label {
        font-size: 15px;
      }

      i {
        bottom: 6px;
      }
    }
  }

  .register-container {
    width: 290px;
  }
}